import { FacebookOutlined, InstagramOutlined, LinkedinOutlined, YoutubeOutlined } from '@ant-design/icons';
import { getAbout } from 'api/about';
import Loading from 'components/loading';
import Particle from 'components/Particle';
import React from 'react';
import { useQuery } from 'react-query';
import { NavLink } from 'react-router-dom';
import styles from './style.module.scss';

function AboutMe() {
  const { data, isFetching } = useQuery('aboutMe', () => getAbout());
  if (isFetching) {
    return <Loading />;
  }
  return (
    <>
      <Particle />
      <div className={styles.container}>
        <div className={styles.aboutInner}>
          <div className={styles.aboutContent}>
            <div className={styles.thumb}>
              <img src={data?.thumbnail} alt="" />
            </div>
            <div className={styles.textContent}>
              <h3>{data?.title}</h3>
              <div className={styles.aboutBio}>
                <p>{data?.content}</p>
              </div>

              <ul className={styles.aboutSocial}>
                <li>
                  <NavLink to={{ pathname: data.facebook }} target={'_blank'}>
                    <FacebookOutlined />
                  </NavLink>
                </li>
                <li>
                  <NavLink to={{ pathname: data.instal }} target={'_blank'}>
                    <InstagramOutlined />
                  </NavLink>
                </li>
                <li>
                  <NavLink to={{ pathname: data.youtube }} target={'_blank'}>
                    <YoutubeOutlined />
                  </NavLink>
                </li>
                <li>
                  <NavLink to={{ pathname: data.linkedin }} target={'_blank'}>
                    <LinkedinOutlined />
                  </NavLink>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default AboutMe;
